import React, {useState} from 'react'
import Video from '../../assets/videos/production ID_4486543.mp4'
import {
    HeroBg, 
    VideoBg, 
    HeroContainer,
    HeroContent, 
    HeroP,
    HeroH1,
    HeroBtnWrapper,
    ArrowForward,
    ArrowRight

} from './HeroSectionStyle'
import{ Button} from '../ButtonStyle'

const HeroSection = () => {

    const [hover, setHover] = useState(false)

    const onHover = () =>{
        setHover(!hover)
    }
    return (
        <>
            <HeroContainer >
                    <HeroBg>
                        <VideoBg  autoPlay loop muted src={Video} type="video/mp4" />
                    </HeroBg>
                    <HeroContent>
                        <HeroH1>Recrutez facilement des bons candidats et donnez de la visibilté à votre entreprise</HeroH1>
                        <HeroP>
                        Votre avanture commence dès maintenant!
                    </HeroP>
                    <HeroBtnWrapper>
                        <Button to='signup' 
                                onMouseEnter={onHover} 
                                onMouseLeave={onHover}
                                primary='true'
                                dark='true'
                                smooth={true}
                                duration={500} 
                                spy={true}
                                exact={true} 
                                offset={-80}
                                >
                                Commencez {hover ? <ArrowForward/> : <ArrowRight/> }
                        </Button>
                    </HeroBtnWrapper>
                    </HeroContent>
            </HeroContainer>
        </>
    )
}

export default HeroSection
