import React from 'react';
import './App.css';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'
import Accueil from './pages/Accueil';
import Emploi from './pages/Emploi';
import Offre from './pages/Offre';
import Entreprise from './pages/Entreprise';
import ProfilEntreprise from './pages/ProfilEntreprise';
import Signin from './pages/Signin';
import Signup from './pages/Signup';
import Recruteur from './pages/recruteur/Recruteur';
import Annonces from './pages/recruteur/Annonces';
import Produits from './pages/recruteur/Produits';
import Tarifs from './pages/recruteur/Tarifs';
import CvTheque from './pages/recruteur/CvTheque';
import CurriculumV from './pages/CurriculumV';
import SigninRecruteur from './pages/recruteur/SigninRecruteur';
import SignupRecruteur from './pages/recruteur/SignupRecruteur';



function App() {
  return (
    <>
        <Router  basename={process.env.PUBLIC_URL} >
              <Switch>
                  <Route  exact path="/" component={Accueil} />
                  <Route  exact path="/emplois" component={Emploi} />
                  <Route  exact path="/offre" component={Offre} />
                  <Route  exact path="/deposercv" component={CurriculumV} />
                  <Route  exact path="/entreprises" component={Entreprise} />
                  <Route  exact path="/profil_entreprise" component={ProfilEntreprise} />
                  <Route  exact path="/connexion" component={Signin} />
                  <Route  exact path="/inscription" component={Signup} />  

                  <Route  exact path="/recruteur" component={Recruteur} />
                  <Route  exact path="/recruteur/annonces" component={Annonces} />   
                  <Route  exact path="/recruteur/produits" component={Produits} /> 
                  <Route  exact path="/recruteur/tarifs" component={Tarifs} />  
                  <Route  exact path="/recruteur/cvtheque" component={CvTheque} /> 
                  <Route  exact path="/recruteur/seconnecter" component={SigninRecruteur} /> 
                  <Route  exact path="/recruteur/sinscrire" component={SignupRecruteur} /> 
                          
              </Switch>
        </Router>
    </>
  );
}

export default App;
