import React,{useState}  from 'react'
import theme from '../components/theme/theme'
import HeaderJob from '../components/HeaderJob/Index'
import { ThemeProvider, Grid  } from '@material-ui/core'
import SearchJob from '../components/SearchJob/index.js'
import JobCard from '../components/Job/JobCard'
import ViewJobModal from '../components/Job/ViewJobModal'
import jobData from '../components/EmploiSection/emploiData'
import Footer from '../components/Footer/Footer'
import ScrollToTop from '../components/Signin/ScrollToTop'

function Offre(props) {
    const [viewJob, setViewJob] = useState(false)
    return (
        <>
           <ThemeProvider theme={theme}>
               <ScrollToTop/>
               <HeaderJob />
               <Grid container justify="center" >
                  <Grid item xs={10} mb={5} >
                        <ViewJobModal
                            job={viewJob} 
                            closeModal={()=> setViewJob({})}
                        />
                        <SearchJob/>
                        {jobData.map(job => 
                            <JobCard  open={()=>setViewJob(job)} key={job.id} {...job} />                
                        )}
                  </Grid>
              </Grid>
              <Footer/>
           </ThemeProvider>
         
           
        </>
    )
}

export default Offre
