import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Controls from '../controls/Controls';
import {useForm,Form} from '../useForm'
import {ContainerHead, FormWrap, Icon} from './FormSinupElements'


const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: '#008647',
    background: '#008647'
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(4),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  ContainerStyle: {
   
    background: '#008647'

  }
}));

const genderItems = [
    { id: 'maxculin', title: 'Max' },
    { id: 'feminin', title: 'Fem' }
    
]



const initialValues = {
    id: 0,
    nom: '',
    prenom:'',
    email: '',
    telephone: '',
    motDePasse: '',
    sexe: 'sexe',
    hireDate: new Date(),
}




export default function FormSinup(props) {


  const {add} = props
  const classes = useStyles();

  const validate = (fieldValues = values) => {
    let temp = { ...errors }
    if ('nom' in fieldValues)
        temp.nom = fieldValues.nom ? "" : "Champ requis."
    if ('prenom' in fieldValues)
        temp.prenom = fieldValues.prenom ? "" : "Champ requis."
    if ('ville' in fieldValues)
        temp.societe = fieldValues.ville ? "" : "Champ requis."
    if ('email' in fieldValues)
        temp.email = (/$^|.+@.+..+/).test(fieldValues.email) ? "" : "Email non valide."
    if ('motDePasse' in fieldValues)
        temp.motDePasse = /[A-Z]/.test(fieldValues.motDePasse) && /[0-9]/.test(fieldValues.motDePasse) && !/[aeiou]/.test(fieldValues.motDePasse) 
        && /^[@#][A-Za-z0-9]{7,13}$/.test(fieldValues.motDePasse) ? "" : "Mot non valide"
    if ('telephone' in fieldValues)
        temp.telephone = fieldValues.telephone.length > 8 ? "" : "Minimum de 9 nombre requis."

    if (fieldValues === values)
        return Object.values(temp).every(x => x === "")
   }

  const {
    values,
    setValues,
    errors,
    setErrors,
    handleInputChange,
    resetForm
  } = useForm(initialValues, true, validate);

  const handleSubmit = e => {
    e.preventDefault()
    if (validate()) {
        add(values, resetForm);
    }
}


  return (
  
    <Container >
        <ContainerHead>
                <FormWrap>
                    <Icon to="/">JOBS GABON</Icon>
                </FormWrap>
        </ContainerHead>
      <Container maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Inscription
        </Typography>
        <Form  onSubmit={handleSubmit} className={classes.form}>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <Controls.Input 
                   name="nom"
                   label="Nom" 
                   value={values.nom}
                   onChange={handleInputChange}
                   error={errors.nom}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
            <Controls.Input 
                   name="prenom"
                   label="Prénom" 
                   value={values.prenom}
                   onChange={handleInputChange}
                   error={errors.prenom}
              />
            </Grid>
            <Grid item xs={12}>
            <Controls.RadioGroup
                    name="type"
                    label="Sexe"
                    value={values.type}
                    onChange={handleInputChange}
                    items={genderItems}
              />
            </Grid>
            <Grid item xs={12}>
            <Controls.Input 
                   name="ville"
                   label="Ville" 
                   value={values.ville}
                   onChange={handleInputChange}
                   error={errors.ville}
              />
            </Grid>
            <Grid item xs={12}>
              <Controls.Input 
                label="Addresse Email"
                name="email"
                value={values.email}
                onChange={handleInputChange}
                error={errors.email}
              />
            </Grid>
            <Grid item xs={12}>
              <Controls.Input 
                  label="Téléphone"
                  name="telephone"
                  value={values.telephone}
                  onChange={handleInputChange}
                  error={errors.telephone}
              />
            </Grid>
            <Grid item xs={12}>
              <Controls.Input 
                name="motDePasse"
                label="Mot de Passe"
                type="password"
                value={values.motDePasse}
                onChange={handleInputChange}
                error={errors.motDePasse} 
                
              />
            </Grid>
            <Grid item xs={12}>
            <Controls.Input 
                name="motDePasse"
                label="Confirmer le mot de passe"
                type="password"
                value={values.motDePasse}
                onChange={handleInputChange}
                error={errors.motDePasse} 
                
              />
            </Grid>
            
          </Grid>
          <Controls.Button
                type="submit"
                text="S'inscrire" />
          <Controls.Button
                text="Effacer"
                color="default"
                onClick={resetForm} />
          <Grid container justify="flex-end">
            <Grid item>
              <Link href="#" variant="body2">
               Vous avez déjà un compte? Se connecter
              </Link>
            </Grid>
          </Grid>
        </Form>
      </div>
      
    </Container>
   </Container>
  );
}